/*-----------------------------------------
# Mixins & Extended Classes
-----------------------------------------*/
/*-----------------------------------------
# Common
-----------------------------------------*/
body {
  position: relative; }

/*-----------------------------------------
# Typography: common
-----------------------------------------*/
body {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 700; }

.text-blue-lighter {
  color: #3fbcd0; }

.subtitle {
  font-weight: 100;
  font-size: 1.5em; }

/*-----------------------------------------
# Forms: Common
-----------------------------------------*/
input.form-control,
textarea.form-control {
  border: 1px solid #ddd;
  border-radius: 0;
  color: #111111;
  line-height: 1.4em;
  padding: 20px;
  width: 100%;
  font-size: 14px;
  background: #fff; }

/*-----------------------------------------
# Forms: Contact Form 7
-----------------------------------------*/
.wpcf7-form-control-wrap {
  margin-bottom: 15px;
  display: block; }

.wpcf7-not-valid-tip {
  text-align: left;
  font-size: 0.9em;
  font-weight: 100; }

/*-----------------------------------------
# Buttons
-----------------------------------------*/
.btn {
  border-radius: 0 !important; }

.btn-outline-info {
  color: #3fbcd0;
  border-color: #3fbcd0; }

.btn-md {
  padding: 10px 30px;
  font-size: 1em; }

/*-----------------------------------------
# Header: masthead
-----------------------------------------*/
.masthead {
  position: relative;
  background-color: #343a40;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding-top: 8rem;
  padding-bottom: 8rem; }
  .masthead .overlay {
    position: absolute;
    background-color: #343a40;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0.5; }
  .masthead h1 {
    font-size: 2rem; }
  @media (min-width: 768px) {
    .masthead {
      padding-top: 9rem;
      padding-bottom: 9rem; }
    .masthead h1 {
      font-size: 3rem; } }
  .masthead.inside-page {
    padding-top: 8rem;
    padding-bottom: 2rem; }

/*-----------------------------------------
# Header: Navigation / Navbar
-----------------------------------------*/
.navbar-dark .navbar-nav .nav-link {
  color: #fafafa; }

@media (min-width: 992px) {
  .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: .75rem;
    padding-left: .75rem; } }

.navbar-brand, .navbar-brand:hover, .navbar-brand:active, .navbar-brand:focus {
  font-weight: 900;
  letter-spacing: 5px;
  text-decoration: line-through; }

/*-----------------------------------------
# Footer
-----------------------------------------*/
#global-footer {
  padding-top: 1rem;
  padding-bottom: 1rem; }

/*-----------------------------------------
# Section
-----------------------------------------*/
section:not(.no-padding) {
  padding-top: 6rem;
  padding-bottom: 6rem; }

body:not(.home) section {
  padding-top: 4rem;
  padding-bottom: 4rem; }

/*-----------------------------------------
# Section: Common
-----------------------------------------*/
.content .blog-title {
  font-size: 2rem; }

.content h3 {
  font-weight: 600;
  font-size: 26px; }

.content img {
  max-width: 100%;
  height: auto !important; }

/*-----------------------------------------
# Section: Services
-----------------------------------------*/
section.services .service-text {
  padding: 3rem; }

section.services .service-img {
  min-height: 30rem;
  background-size: cover; }

@media (min-width: 768px) {
  section.services .service-text {
    padding: 6rem; } }

/*-----------------------------------------
# Section: Features
-----------------------------------------*/
section.features i {
  font-size: 100px;
  height: 100px;
  width: 100px;
  text-align: center;
  display: inline-block;
  margin: 0 auto 30px;
  color: #17a2b8; }

/*-----------------------------------------
# Section: Testimonials
-----------------------------------------*/
section.testimonials .testimonial-item {
  max-width: 18rem; }
  section.testimonials .testimonial-item img {
    max-width: 12rem;
    box-shadow: 0px 5px 5px 0px #adb5bd; }

/*-----------------------------------------
# Section: Contact 2
-----------------------------------------*/
section.contact-2 .info-box {
  color: #111111;
  text-align: center;
  box-shadow: 0 0 30px rgba(214, 215, 216, 0.3);
  padding: 30px 0 30px 0; }
  section.contact-2 .info-box i {
    font-size: 32px;
    color: #17a2b8;
    border-radius: 50%;
    padding: 8px;
    border: 1px solid #17a2b8;
    margin-bottom: 15px; }
  section.contact-2 .info-box h3 {
    font-size: 20px;
    font-weight: 700;
    margin: 10px 0; }
  section.contact-2 .info-box p {
    padding: 0;
    line-height: 24px;
    font-size: 14px;
    margin-bottom: 0; }

/*-----------------------------------------
# Section: Services 2
-----------------------------------------*/
section.services-2 .icon-box {
  text-align: center;
  border: 1px solid #d4f5fa;
  padding: 40px 30px;
  transition: all ease-in-out 0.3s;
  background: white; }
  section.services-2 .icon-box .icon {
    margin: 0 auto;
    width: 70px;
    height: 70px;
    background: #d4f5fa;
    border-radius: 50%;
    border: 1px solid #deebfd;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    transition: ease-in-out 0.3s; }
    section.services-2 .icon-box .icon i {
      color: #17a2b8;
      font-size: 38px;
      transition: ease-in-out 0.3s; }
  section.services-2 .icon-box h4 {
    font-weight: 700;
    margin-bottom: 15px;
    font-size: 24px;
    transition: ease-in-out 0.3s; }
  section.services-2 .icon-box p {
    line-height: 24px;
    font-size: 1em;
    margin-bottom: 0; }
  section.services-2 .icon-box:hover {
    border-color: #fafafa;
    box-shadow: 0px 0 25px 0 rgba(188, 190, 191, 0.3); }
  section.services-2 .icon-box:hover h4, section.services-2 .icon-box:hover .icon i {
    color: #17a2b8; }
  section.services-2 .icon-box:hover .icon {
    border-color: #17a2b8; }

/*-----------------------------------------
# Section: Blog
-----------------------------------------*/
section.blog .blog-article, section.blog .highlighted-article {
  background-color: #fff;
  margin-bottom: 48px;
  transition: box-shadow .15s ease; }
  section.blog .blog-article, section.blog .blog-article .entry-image, section.blog .highlighted-article, section.blog .highlighted-article .entry-image {
    position: relative; }
  section.blog .blog-article time, section.blog .highlighted-article time {
    position: absolute;
    right: 8px;
    bottom: 8px;
    background: rgba(17, 17, 17, 0.7);
    font-size: .875rem;
    color: #fff;
    padding: 2px 4px; }
  section.blog .blog-article .entry-title, section.blog .highlighted-article .entry-title {
    font-size: 20px; }
    section.blog .blog-article .entry-title a, section.blog .blog-article .entry-title a:hover, section.blog .highlighted-article .entry-title a, section.blog .highlighted-article .entry-title a:hover {
      color: #111111;
      text-decoration: none; }
  section.blog .blog-article .entry-summary a, section.blog .highlighted-article .entry-summary a {
    color: #17a2b8; }

section.blog .blog-article {
  box-shadow: 0 2px 4px 0 rgba(162, 164, 166, 0.3); }
  section.blog .blog-article:hover {
    box-shadow: 0 8px 10px 0 rgba(162, 164, 166, 0.3); }
  section.blog .blog-article .entry-image img {
    width: 100%;
    height: 190px;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: 0 0;
    object-position: 0 0; }
  section.blog .blog-article .entry-content {
    padding: 24px; }

section.blog .highlighted-article {
  box-shadow: 0 2px 12px 0 rgba(162, 164, 166, 0.3); }
  section.blog .highlighted-article:hover {
    box-shadow: 0 8px 10px 0 rgba(162, 164, 166, 0.3); }
  section.blog .highlighted-article .entry-image img {
    width: 100%;
    height: 300px;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: 50% 50%;
    object-position: 50% 50%; }
  section.blog .highlighted-article time {
    right: 23px; }
  section.blog .highlighted-article .entry-content {
    padding: 24px 39px !important; }
  @media (min-width: 768px) {
    section.blog .highlighted-article .entry-title {
      font-size: 28px; }
    section.blog .highlighted-article .entry-content {
      padding: 24px 40px 24px 15px !important; }
    section.blog .highlighted-article .entry-image {
      width: 100%;
      height: 100%;
      max-width: 100%; } }

/*-----------------------------------------
# Utils
-----------------------------------------*/
.img-bg {
  background-position: center center;
  background-repeat: no-repeat; }

.img-crop-circle {
  width: 300px;
  max-width: 100%;
  height: auto;
  position: relative;
  overflow: hidden;
  border-radius: 50%; }

.vcenter-container {
  position: relative;
  height: 100%; }
  .vcenter-container .vcenter-content.vcenter-mobile-off {
    position: relative; }
    @media (min-width: 768px) {
      .vcenter-container .vcenter-content.vcenter-mobile-off {
        margin: 0;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%); } }

.sticky-header {
  position: fixed;
  z-index: 999;
  width: 100%;
  top: 0; }
  .sticky-header + div {
    margin-top: 65px; }
